import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
// import { PipesModule } from '../../pipes/pipes.module';

import { PropertyDetailPage } from './property-detail';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import {UserProvider} from '../../providers/user/user';
import { HttpClient, HttpHeaders, HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import {ThousandSuffixesPipe} from "./orderby.pipe"
import { ChartsModule } from 'ng2-charts';

@NgModule({
	declarations: [
		PropertyDetailPage,ThousandSuffixesPipe
	],
	imports: [
		IonicPageModule.forChild(PropertyDetailPage), HttpModule, ChartsModule
	],
	exports: [
		PropertyDetailPage
	],
	providers:[InAppBrowser, UserProvider]
})

export class PropertyListPageModule { }



