import { Component } from '@angular/core';
import { IonicPage, AlertController,LoadingController, ActionSheetController, ActionSheet, NavController, NavParams, ToastController } from 'ionic-angular';

import { PropertyService } from '../../providers/property-service-mock';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { UserProvider } from '../../providers/user/user';
import leaflet from 'leaflet';
declare let L;
declare var cordova: any;
@IonicPage({
    name: 'page-property-detail',
    segment: 'property/:id'
})

@Component({
    selector: 'page-property-detail',
    templateUrl: 'property-detail.html'
})
export class PropertyDetailPage {

    property: any;
    param: number;
    images: any;
    descrption: any;
    profileName: any;
    show: boolean = false;
    data1: any;
    legalid: any;
    propertyItem: any;
    propertyName: any;
    map;
    markersGroup;
    properties: Array<any>;
    yield: any;
    data: any;
    timezero: any;
    show1: boolean = true;
    show2: boolean = false;
    check: boolean;
    legalId:any;



    public lineChartData: Array<any> = [
        { data: [40, 38, 41, 43, 47], label: 'Earnings' },
        { data: [0, 0, 3, 5, 9], label: 'Dividends' }
    ];
    public lineChartLabels: Array<any> = ['2013', '2014', '2015', '2016', '2017'];
    public lineChartType: string = 'line';
    public pieChartType: string = 'pie';

    // Pie
    public pieChartLabels: string[] = ['Download Sales', 'In-Store Sales', 'Mail Sales'];
    public pieChartData: number[] = [300, 500, 100];

    public randomizeType(): void {
        this.lineChartType = this.lineChartType === 'line' ? 'bar' : 'line';
        this.pieChartType = this.pieChartType === 'doughnut' ? 'pie' : 'doughnut';
    }

    public chartClicked(e: any): void {
        console.log(e);
    }

    public chartHovered(e: any): void {
        console.log(e);
    }


    constructor(public actionSheetCtrl: ActionSheetController,public loadingCtrl: LoadingController, public alert: AlertController, public user: UserProvider, public iab: InAppBrowser, public navCtrl: NavController, public navParams: NavParams, public propertyService: PropertyService, public toastCtrl: ToastController) {
        
        this.showAlldata();
       
    }


    ngOnInit(){
        this.showMap();
    }
    // ngOnInit() {
    //     const map = L.map('map').setView([51.505, -0.09], 13);

    //     L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    //         attribution: ' <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
    //     }).addTo(map);
    // }


    showAlldata() {

        this.legalId = this.navParams.get('legalId');
        console.log(this.legalId);

        if(this.legalId != null && this.legalId != undefined){
            let loading = this.loadingCtrl.create({
                content: 'Loading property data...'
            });
            loading.present();
    
            this.user.legallist1(this.legalId).subscribe(
                data => {
    
    
                    let datas = JSON.parse(JSON.stringify(data));
                    let data2 = JSON.parse(datas._body);
    
                    this.check = this.user.checkDataEmpty(data2.data);
                    console.log(this.check);
                    if (this.check) {
                        this.navCtrl.setRoot('page-general_error');
    
                    } else {
    
                        
                        this.param = data2.data[0];
                        // console.log(this.param);
                        // this.legalid = this.param['LegalId'];
                        this.yield = this.param['property'][0].AskingYield;
                        // console.log(this.param);
                        this.property = this.param;
                        console.log(this.property);
                        
                        this.images = this.property.property[0].Images;
                        this.descrption = this.property.property[0].Description;
                        this.propertyName = this.property.property[0].Name;
                        if (this.property.property[0].profile.length === 0) {
                
                        } else {
                            this.profileName = this.property.property[0].profile[0].name;
                            if (this.profileName != null) {
                                this.show = true;
                            }
                        }
                
                
                
                        // console.log(this.images);
    
                    }
    
    
                },
                error => {
                    console.log(error.JSON);
                    setTimeout(() => {
                        loading.dismiss();
                        this.navCtrl.setRoot('page-general_error');
                      }, 1000);
                },
                () => {
    
    
                    loading.dismiss();
    
    
                }
            )

        }
       


        // console.log(this.legalid, this.yield);
        this.user.finance().subscribe(
            data => {
                let datas = JSON.parse(JSON.stringify(data));
                let data2 = JSON.parse(datas._body);
                console.log();
                this.check = this.user.checkDataEmpty(data2.data);
                console.log(this.check);
                if (this.check) {
                    this.navCtrl.setRoot('page-general_error');

                } else {

                    this.data = data2.data;

                    console.log(this.data);
                    this.timezero = data2.data[0];
                    console.log(this.timezero);


                }

            },error => {
				console.log(error.JSON);
				setTimeout(() => {
	      this.navCtrl.setRoot('page-general_error');
				  }, 1000);
			},
    )

       
    }


    doRefresh(refresher) {

        this.showAlldata();

        setTimeout(() => {

            refresher.complete();
        }, 2000);
    }




    openBrokerDetail(broker) {
        this.navCtrl.push('page-broker-detail', {
            'id': broker.id
        });
    }


    // go to pdf file

    go() {



        var ref = "https://www.dreamblock.co.za/" + this.profileName;

        // window.open(encodeURI(ref), '_blank', 'location=yes,EnableViewPortScale=yes');
        // // browser.close();
        //     console.log(ref);

        var ref1 = cordova.InAppBrowser.open(ref, '_system', 'location=yes,enableViewportScale=yes,hidden=no');
        ref1.addEventListener('loadstart', function (event) { console.log(event.url); });

    }

    favorite(property) {
        this.propertyService.favorite(property)
            .then(property => {
                let toast = this.toastCtrl.create({
                    message: 'Property added to your favorites',
                    cssClass: 'mytoast',
                    duration: 2000
                });
                toast.present(toast);
            });
    }

    share(property) {

        if (localStorage.getItem("token") === null) {

            let toast = this.toastCtrl.create({
                message: 'Please log in to invest',
                duration: 3000,
                position: 'top'
            });

            toast.onDidDismiss(() => {
                this.navCtrl.setRoot('page-auth');
            });

            toast.present();



        } else {
            this.navCtrl.push('page-checkout', { 'property': property });
        }
    }


    goTopropertyList() {
        console.log(this.property);
        this.navCtrl.push('page-property-list1', { 'propertyDetails': this.property });
    }

    showMap() {
        var info = leaflet.control();

        setTimeout(() => {

           this.map = L.map('map').setView([51.505, -0.09], 13);

            L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                attribution: ' <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(this.map);
            this.map.dragging.disable();
            this.map.touchZoom.disable();
            this.map.doubleClickZoom.disable();
            this.map.scrollWheelZoom.disable();
            this.map.boxZoom.disable();
            this.map.keyboard.disable();
            if (this.map.tap) this.map.tap.disable();
            this.showMarkers();

        })
    }

    showMarkers() {
        if (this.markersGroup) {
            this.map.removeLayer(this.markersGroup);
        }
        this.markersGroup = leaflet.layerGroup([]);
        // this.properties.forEach(property => {
        //     if (property.lat, property.long) {
        //         let marker: any = leaflet.marker([property.lat, property.long]).on('click', event => this.openPropertyDetail(event.target.data));
        //         marker.data = property;
        //         this.markersGroup.addLayer(marker);
        //     }
        // });
        this.map.addLayer(this.markersGroup);
    }

    // read text full
    read() {

        this.show1 = false;
        this.show2 = true;

    }

}



